body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.ant-layout-has-sider .trigger {
  font-size: 18px;
  line-height: 64px;
  padding: 0 16px;
  cursor: pointer;
  transition: color .3s; }

.ant-layout-has-sider .trigger:hover {
  color: #108ee9; }

.ant-layout-has-sider .logoNameCont {
  width: 220px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  color: #fff;
  font-size: 16px;
  margin: 5px 0; }

.ant-layout-has-sider .logoNameCont .logo {
  height: 30px;
  width: 30px;
  background: url(data:image/png;base64,) no-repeat center center;
  background-size: 100%;
  border-radius: 6px;
  margin: 10px 10px 10px 0; }

/* // Header */
.ant-menu-submenu-horizontal .ant-menu-vertical .logout {
  padding: 0; }

.logout span {
  display: inline-block;
  width: 100%;
  height: 100%; }

.mindex {
  min-height: 360; }
  .mindex .avatar {
    text-align: center;
    padding: 50px 0;
    line-height: 25px;
    background: url(/static/media/avatar_bg.ea11b856.jpg) no-repeat center center/cover; }
  .mindex .weather {
    overflow: hidden;
    padding: 10px 0;
    width: 120px;
    margin: 0 auto; }
    .mindex .weather .weather-img {
      float: left; }
      .mindex .weather .weather-img img {
        width: 45px;
        height: 35px; }
    .mindex .weather .weather-info {
      float: left;
      line-height: 35px;
      height: 35px; }
  .mindex .pro {
    text-align: center;
    margin-top: 10px; }
    .mindex .pro p {
      height: 30px;
      line-height: 30px;
      font-size: 14px; }
    .mindex .pro #pro1 .ant-progress-circle-path {
      stroke: red;
      opacity: .6; }
    .mindex .pro #pro2 .ant-progress-circle-path {
      stroke: blue;
      opacity: .6; }
    .mindex .pro #pro3 .ant-progress-circle-path {
      stroke: purple;
      opacity: .6; }
  .mindex .timeline {
    margin-top: 22px; }
    .mindex .timeline .timeItem {
      line-height: 25px; }
  .mindex .collapse {
    margin-top: 22px; }
    .mindex .collapse .ant-collapse {
      background: #fff;
      border: 0; }
      .mindex .collapse .ant-collapse .ant-collapse-item {
        margin-bottom: 15px; }
        .mindex .collapse .ant-collapse .ant-collapse-item .ant-collapse-header {
          color: #fff;
          background: lightcoral;
          letter-spacing: 5px; }
          .mindex .collapse .ant-collapse .ant-collapse-item .ant-collapse-header .arrow {
            letter-spacing: 0;
            color: #fff; }
        .mindex .collapse .ant-collapse .ant-collapse-item .ant-collapse-content {
          padding: 10px 20px; }
          .mindex .collapse .ant-collapse .ant-collapse-item .ant-collapse-content p {
            line-height: 25px; }
          .mindex .collapse .ant-collapse .ant-collapse-item .ant-collapse-content .author {
            text-align: right; }
  .mindex .ant-table-thead > tr > th, .mindex .ant-table-tbody > tr > td {
    text-align: center; }
  .mindex th.column-written,
  .mindex td.column-written {
    text-align: left !important;
    padding-left: 5%; }

.login {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 100%;
  background: #f3f3f3; }
  .login .login-form {
    width: 320px;
    padding: 36px;
    box-shadow: 0 0 100px rgba(0, 0, 0, 0.08);
    background: #fff; }
    .login .login-form .login-logo {
      width: 100%;
      height: 40px;
      background: url(/static/media/sun.2aeb66ed.png) no-repeat 60px center/15%;
      margin-bottom: 20px; }
      .login .login-form .login-logo .login-name {
        line-height: 40px;
        text-align: center;
        padding-left: 15px;
        font-size: 20px; }
    .login .login-form .githubUrl {
      display: block;
      margin: 0 auto;
      width: 25px;
      height: 25px;
      background: url(/static/media/github.26ae8e57.png) no-repeat center center/cover; }

.loading {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 100%; }

.App {
  height: 100vh;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

