.ant-layout-has-sider .trigger {
    font-size: 18px;
    line-height: 64px;
    padding: 0 16px;
    cursor: pointer;
    transition: color .3s;
}

.ant-layout-has-sider .trigger:hover {
    color: #108ee9;
}

.ant-layout-has-sider .logoNameCont {
    width: 220px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    font-size: 16px;
    margin: 5px 0;
}

.ant-layout-has-sider .logoNameCont .logo {
    height: 30px;
    width: 30px;
    background: url('./img/logo.png') no-repeat center center;
    background-size: 100%;
    border-radius: 6px;
    margin: 10px 10px 10px 0;
}

/* // Header */
.ant-menu-submenu-horizontal .ant-menu-vertical .logout{
    padding: 0;
}
.logout span{
    display: inline-block;
    width: 100%;
    height: 100%;
}

